<template>
    <div id="new-item">
        <h3>New Item</h3>
        <div class="row">
            <form class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="item_id" required>
                            <label>Item ID</label>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                            <label>Item Name</label>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="type" required>
                            <label>Type of Item</label>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="date" required>
                            <label>Date</label>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="quantity" required>
                            <label>Quantity</label>
                    </div>
                    <button type="submit" @click="saveItem" class="btn green">Submit</button>
                    <router-link to="/" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-item',
    data () {
        return {
            item_id: null,
            name: null,
            type: null,
            date: null,
            quantity: null
        }
    },
    methods: {
        saveItem () {
            db.collection('inventory').add({
                item_id: this.item_id,
                name: this.name,
                type: this.type,
                date: this.date,
                quantity: this.quantity
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>


