<template>
    <div id="edit-item">
        <h3>Edit Item</h3>
        <div class="row">
            <form @submit.prevent="updateItem" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="item_id" required>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="type" required>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="date" required>
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="quantity" required>
                    </div>
                    <button @click="updateItem" type="submit" class="btn green">Submit</button>
                    <router-link to="/" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-item',
    data () {
        return {
            item_id: null,
            name: null,
            type: null,
            date: null,
            quantity: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('inventory').where('item_id', '==', to.params.item_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.item_id = doc.data().item_id
                    vm.name = doc.data().name
                    vm.type = doc.data().type
                    vm.date = doc.data().date
                    vm.quantity = doc.data().quantity
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('inventory').where('item_id', '==', this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.item_id = doc.data().item_id
                    this.name = doc.data().name
                    this.type = doc.data().type
                    this.date = doc.data().date
                    this.quantity = doc.data().quantity
                })
            })
        },
        updateItem() {
            db.collection('inventory').where('item_id', '==', this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        item_id: this.item_id,
                        name: this.name,
                        type: this.type,
                        date: this.date,
                        quantity: this.quantity
                    })
                    .then(() => {
                        this.$router.push({name: 'view-item', params: {item_id: this.item_id}})
                    })
                })
            })
        }
    }
}
</script>


