<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Items</h4>
            </li>
            <li v-for="inventory in inventory" v-bind:key="inventory.id" class="collection-item">
                <div class="chip number" :class="{ 'low-quantity': inventory.quantity < 1 }">{{inventory.quantity}}</div>
                {{inventory.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-item', params: {item_id: inventory.item_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            inventory: []
        }
    },
    created () {
        db.collection('inventory').orderBy('type').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.data);
                const data = {
                    'id': doc.id,
                    'item_id': doc.data().item_id,
                    'name': doc.data().name,
                    'date': doc.data().date,
                    'quantity': doc.data().quantity,
                    'type': doc.data().type
                }
                this.inventory.push(data)
            })
        })
    }
}
</script>

<style scoped>

.low-quantity {
    background-color: red;
    color: white;
}
   
</style>


