<template>
    <div id="view-item">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">Type: {{type}}</li>
            <li class="collection-item">Date: {{date}}</li>
            <li class="collection-item">Quantity: {{quantity}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="goAmazon" class="btn orange">Order</button>
        <button @click="deleteItem" class="btn red">Delete</button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-item', params: {item_id: item_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-item',
    data () {
        return {
            item_id: null,
            name: null,
            type: null,
            date: null,
            quantity: null        
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('inventory').where('item_id', '==', to.params.item_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.item_id = doc.data().item_id
                    vm.name = doc.data().name
                    vm.type = doc.data().type
                    vm.date = doc.data().date
                    vm.quantity = doc.data().quantity
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('inventory').where('item_id', '==', this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.item_id = doc.data().item_id
                    this.name = doc.data().name
                    this.type = doc.data().type
                    this.date = doc.data().date
                    this.quantity = doc.data().quantity
                })
            })
        },
        deleteItem () {
            if(confirm('Are you sure?')) {
                db.collection('inventory').where('item_id', '==', this.$route.params.item_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            }
        },
        goAmazon () {
            window.open("https://www.amazon.com", "_blank")
        }
    }
}
</script>


